import { GetStaticPaths, GetStaticProps } from "next";
import { useRouter } from "next/router";
import Header from "../../components/Header";
import AvatarSection from "../../components/AvatarSection";
import { getAllDiaries, getDiaryBySlug, Diary } from "../../lib/diary";

interface DiaryPageProps {
  diary: Diary | null;
}

export default function DiaryPage({ diary }: DiaryPageProps) {
  const router = useRouter();

  // È°µÈù¢ËøòÂú®ÊûÑÂª∫Êó∂
  if (router.isFallback) {
    return <div>Loading...</div>;
  }

  if (!diary) {
    return <div>Diary not found</div>;
  }

  return (
    <div className="container">
      <Header />
      <AvatarSection />
      <article style={{ margin: "2rem 0" }}>
        <h1 style={{ fontSize: "2rem", fontWeight: "bold" }}>{diary.title}</h1>
        <p style={{ color: "#888", marginBottom: "1rem" }}>{diary.date}</p>
        {diary.cover && (
          <img
            src={diary.cover}
            alt={diary.title}
            style={{ maxWidth: "100%", marginBottom: "1rem" }}
          />
        )}
        <div
          dangerouslySetInnerHTML={{ __html: diary.content }}
          style={{ lineHeight: 1.7 }}
        />
      </article>
    </div>
  );
}

export const getStaticPaths: GetStaticPaths = async () => {
  const diaries = getAllDiaries();
  const paths = diaries.map((diary) => ({
    params: { diary: diary.slug },
  }));

  return {
    paths,
    fallback: true, // ÊûÑÂª∫Êó∂Ê≤°ÊúâÁöÑË∑ØÂæÑ‰ºöÁîüÊàê loading
  };
};

export const getStaticProps: GetStaticProps = async ({ params }) => {
  const slug = params?.diary as string;
  const diary = getDiaryBySlug(slug);

  return {
    props: {
      diary,
    },
  };
};
